import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Calendar } from "@/components/ui/calendar"
import {
  Users,
  Activity,
  FileText,
  Calendar as CalendarIcon,
  Bell,
  Wallet,
  Upload,
  Brain,
  AlertCircle,
  TrendingUp,
  Heart,
  Thermometer,
  Weight,
  BarChart3,
  Clock,
  MapPin,
  Phone,
  Mail,
  Plus,
  Settings,
  Download,
} from "lucide-react"
import Link from "next/link"

export default function PatientDashboard() {
  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50/50 to-green-50/50">
      {/* Header */}
      <div className="border-b bg-white/80 backdrop-blur-sm sticky top-0 z-40">
        <div className="container py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Avatar className="h-10 w-10">
                <AvatarImage src="/placeholder-user.jpg" />
                <AvatarFallback>JD</AvatarFallback>
              </Avatar>
              <div>
                <h1 className="text-xl font-semibold">Welcome back, John!</h1>
                <p className="text-sm text-muted-foreground">Here's your health overview</p>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <Button variant="ghost" size="sm">
                <Bell className="h-4 w-4" />
                <Badge className="ml-1 h-4 w-4 rounded-full p-0 text-xs">3</Badge>
              </Button>
              <Button variant="ghost" size="sm">
                <Settings className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </div>
      </div>

      <div className="container py-8">
        <div className="grid gap-6">
          {/* Quick Stats */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <Card className="card-hover">
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-primary/10 rounded-lg">
                    <Users className="h-5 w-5 text-primary" />
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Family Members</p>
                    <p className="text-2xl font-bold">4</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card className="card-hover">
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-secondary/10 rounded-lg">
                    <CalendarIcon className="h-5 w-5 text-secondary" />
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Upcoming Appointments</p>
                    <p className="text-2xl font-bold">2</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card className="card-hover">
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-accent/10 rounded-lg">
                    <FileText className="h-5 w-5 text-accent" />
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Health Records</p>
                    <p className="text-2xl font-bold">24</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card className="card-hover">
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-green-100 rounded-lg">
                    <Activity className="h-5 w-5 text-green-600" />
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Health Score</p>
                    <p className="text-2xl font-bold text-green-600">85%</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Main Dashboard Content */}
          <div className="grid lg:grid-cols-3 gap-6">
            {/* Left Column */}
            <div className="lg:col-span-2 space-y-6">
              {/* Family Profiles */}
              <Card>
                <CardHeader className="flex flex-row items-center justify-between">
                  <div>
                    <CardTitle>Family Profiles</CardTitle>
                    <CardDescription>Manage health records for your family</CardDescription>
                  </div>
                  <Button size="sm">
                    <Plus className="h-4 w-4 mr-1" />
                    Add Member
                  </Button>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                    {[
                      { name: "John Doe", age: 35, avatar: "JD", status: "healthy" },
                      { name: "Jane Doe", age: 32, avatar: "JA", status: "checkup" },
                      { name: "Emma Doe", age: 8, avatar: "ED", status: "healthy" },
                      { name: "Max Doe", age: 5, avatar: "MD", status: "vaccination" },
                    ].map((member, index) => (
                      <div key={index} className="text-center p-4 border rounded-lg card-hover cursor-pointer">
                        <Avatar className="h-12 w-12 mx-auto mb-2">
                          <AvatarFallback>{member.avatar}</AvatarFallback>
                        </Avatar>
                        <p className="font-medium text-sm">{member.name}</p>
                        <p className="text-xs text-muted-foreground">{member.age} years</p>
                        <Badge 
                          variant={member.status === 'healthy' ? 'default' : 'secondary'}
                          className="mt-1 text-xs"
                        >
                          {member.status}
                        </Badge>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>

              {/* Vitals Tracking */}
              <Card>
                <CardHeader>
                  <CardTitle>Vital Signs Tracking</CardTitle>
                  <CardDescription>Monitor your health metrics over time</CardDescription>
                </CardHeader>
                <CardContent>
                  <Tabs defaultValue="blood-pressure" className="w-full">
                    <TabsList className="grid w-full grid-cols-4">
                      <TabsTrigger value="blood-pressure">BP</TabsTrigger>
                      <TabsTrigger value="heart-rate">HR</TabsTrigger>
                      <TabsTrigger value="weight">Weight</TabsTrigger>
                      <TabsTrigger value="temperature">Temp</TabsTrigger>
                    </TabsList>
                    
                    <TabsContent value="blood-pressure" className="space-y-4">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-2">
                          <Heart className="h-5 w-5 text-red-500" />
                          <span className="font-medium">Blood Pressure</span>
                        </div>
                        <Button variant="outline" size="sm">
                          <Plus className="h-4 w-4 mr-1" />
                          Log Reading
                        </Button>
                      </div>
                      <div className="grid grid-cols-2 gap-4">
                        <div className="p-4 bg-muted/30 rounded-lg">
                          <p className="text-sm text-muted-foreground">Latest Reading</p>
                          <p className="text-xl font-bold">120/80</p>
                          <p className="text-xs text-green-600">Normal</p>
                        </div>
                        <div className="p-4 bg-muted/30 rounded-lg">
                          <p className="text-sm text-muted-foreground">Average (30 days)</p>
                          <p className="text-xl font-bold">118/78</p>
                          <p className="text-xs text-green-600">Good</p>
                        </div>
                      </div>
                      <div className="h-40 bg-muted/20 rounded-lg flex items-center justify-center">
                        <BarChart3 className="h-8 w-8 text-muted-foreground" />
                        <span className="ml-2 text-muted-foreground">Chart visualization</span>
                      </div>
                    </TabsContent>

                    <TabsContent value="heart-rate">
                      <div className="space-y-4">
                        <div className="flex items-center gap-2">
                          <Activity className="h-5 w-5 text-red-500" />
                          <span className="font-medium">Heart Rate</span>
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                          <div className="p-4 bg-muted/30 rounded-lg">
                            <p className="text-sm text-muted-foreground">Resting HR</p>
                            <p className="text-xl font-bold">68 bpm</p>
                          </div>
                          <div className="p-4 bg-muted/30 rounded-lg">
                            <p className="text-sm text-muted-foreground">Max HR Today</p>
                            <p className="text-xl font-bold">142 bpm</p>
                          </div>
                        </div>
                      </div>
                    </TabsContent>

                    <TabsContent value="weight">
                      <div className="space-y-4">
                        <div className="flex items-center gap-2">
                          <Weight className="h-5 w-5 text-blue-500" />
                          <span className="font-medium">Weight</span>
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                          <div className="p-4 bg-muted/30 rounded-lg">
                            <p className="text-sm text-muted-foreground">Current</p>
                            <p className="text-xl font-bold">70.5 kg</p>
                          </div>
                          <div className="p-4 bg-muted/30 rounded-lg">
                            <p className="text-sm text-muted-foreground">Goal</p>
                            <p className="text-xl font-bold">68 kg</p>
                          </div>
                        </div>
                        <Progress value={75} className="w-full" />
                        <p className="text-sm text-muted-foreground">75% to goal</p>
                      </div>
                    </TabsContent>

                    <TabsContent value="temperature">
                      <div className="space-y-4">
                        <div className="flex items-center gap-2">
                          <Thermometer className="h-5 w-5 text-orange-500" />
                          <span className="font-medium">Temperature</span>
                        </div>
                        <div className="p-4 bg-muted/30 rounded-lg">
                          <p className="text-sm text-muted-foreground">Latest</p>
                          <p className="text-xl font-bold">98.6°F</p>
                          <p className="text-xs text-green-600">Normal</p>
                        </div>
                      </div>
                    </TabsContent>
                  </Tabs>
                </CardContent>
              </Card>

              {/* Recent Health Records */}
              <Card>
                <CardHeader className="flex flex-row items-center justify-between">
                  <div>
                    <CardTitle>Recent Health Records</CardTitle>
                    <CardDescription>Your latest medical documents</CardDescription>
                  </div>
                  <Button size="sm">
                    <Upload className="h-4 w-4 mr-1" />
                    Upload
                  </Button>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    {[
                      { type: "Lab Report", date: "Nov 20, 2024", doctor: "Dr. Smith", status: "reviewed" },
                      { type: "Prescription", date: "Nov 18, 2024", doctor: "Dr. Johnson", status: "active" },
                      { type: "X-Ray", date: "Nov 15, 2024", doctor: "Dr. Brown", status: "normal" },
                      { type: "Blood Test", date: "Nov 10, 2024", doctor: "Dr. Smith", status: "reviewed" },
                    ].map((record, index) => (
                      <div key={index} className="flex items-center justify-between p-3 border rounded-lg card-hover cursor-pointer">
                        <div className="flex items-center gap-3">
                          <div className="p-2 bg-primary/10 rounded">
                            <FileText className="h-4 w-4 text-primary" />
                          </div>
                          <div>
                            <p className="font-medium text-sm">{record.type}</p>
                            <p className="text-xs text-muted-foreground">{record.date} • {record.doctor}</p>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          <Badge variant="secondary" className="text-xs">{record.status}</Badge>
                          <Button variant="ghost" size="sm">
                            <Download className="h-3 w-3" />
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Right Column */}
            <div className="space-y-6">
              {/* AI Health Insights */}
              <Card className="border-primary/20 bg-gradient-to-br from-primary/5 to-secondary/5">
                <CardHeader>
                  <div className="flex items-center gap-2">
                    <Brain className="h-5 w-5 text-primary" />
                    <CardTitle>AI Health Insights</CardTitle>
                  </div>
                  <CardDescription>Personalized recommendations for you</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="p-3 bg-white/50 rounded-lg">
                    <div className="flex items-start gap-2">
                      <TrendingUp className="h-4 w-4 text-green-500 mt-0.5" />
                      <div>
                        <p className="text-sm font-medium">Great Progress!</p>
                        <p className="text-xs text-muted-foreground">Your blood pressure is trending downward</p>
                      </div>
                    </div>
                  </div>
                  <div className="p-3 bg-white/50 rounded-lg">
                    <div className="flex items-start gap-2">
                      <AlertCircle className="h-4 w-4 text-orange-500 mt-0.5" />
                      <div>
                        <p className="text-sm font-medium">Checkup Due</p>
                        <p className="text-xs text-muted-foreground">Schedule your annual physical</p>
                      </div>
                    </div>
                  </div>
                  <div className="p-3 bg-white/50 rounded-lg">
                    <div className="flex items-start gap-2">
                      <Clock className="h-4 w-4 text-blue-500 mt-0.5" />
                      <div>
                        <p className="text-sm font-medium">Medication Reminder</p>
                        <p className="text-xs text-muted-foreground">Take your vitamins at 8 AM</p>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Upcoming Appointments */}
              <Card>
                <CardHeader>
                  <CardTitle>Upcoming Appointments</CardTitle>
                  <CardDescription>Your scheduled visits</CardDescription>
                </CardHeader>
                <CardContent className="space-y-3">
                  {[
                    {
                      doctor: "Dr. Sarah Smith",
                      specialty: "Cardiologist",
                      date: "Nov 25, 2024",
                      time: "10:00 AM",
                      location: "City Hospital",
                    },
                    {
                      doctor: "Dr. Mike Johnson",
                      specialty: "General Physician",
                      date: "Dec 2, 2024",
                      time: "2:30 PM",
                      location: "MediClinic",
                    },
                  ].map((appointment, index) => (
                    <div key={index} className="p-3 border rounded-lg">
                      <div className="flex justify-between items-start mb-2">
                        <div>
                          <p className="font-medium text-sm">{appointment.doctor}</p>
                          <p className="text-xs text-muted-foreground">{appointment.specialty}</p>
                        </div>
                        <Badge variant="outline" className="text-xs">
                          {appointment.date}
                        </Badge>
                      </div>
                      <div className="flex items-center gap-4 text-xs text-muted-foreground">
                        <div className="flex items-center gap-1">
                          <Clock className="h-3 w-3" />
                          {appointment.time}
                        </div>
                        <div className="flex items-center gap-1">
                          <MapPin className="h-3 w-3" />
                          {appointment.location}
                        </div>
                      </div>
                    </div>
                  ))}
                  <Button variant="outline" size="sm" className="w-full">
                    <Plus className="h-4 w-4 mr-1" />
                    Book New Appointment
                  </Button>
                </CardContent>
              </Card>

              {/* Health Wallet */}
              <Card>
                <CardHeader>
                  <div className="flex items-center gap-2">
                    <Wallet className="h-5 w-5 text-primary" />
                    <CardTitle>Health Wallet</CardTitle>
                  </div>
                  <CardDescription>Track your medical expenses</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="p-3 bg-muted/30 rounded-lg">
                    <p className="text-sm text-muted-foreground">This Month</p>
                    <p className="text-2xl font-bold">$342</p>
                    <p className="text-xs text-green-600">12% less than last month</p>
                  </div>
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span>Consultations</span>
                      <span>$200</span>
                    </div>
                    <div className="flex justify-between text-sm">
                      <span>Medications</span>
                      <span>$89</span>
                    </div>
                    <div className="flex justify-between text-sm">
                      <span>Lab Tests</span>
                      <span>$53</span>
                    </div>
                  </div>
                  <Button variant="outline" size="sm" className="w-full">
                    View Details
                  </Button>
                </CardContent>
              </Card>

              {/* Quick Actions */}
              <Card>
                <CardHeader>
                  <CardTitle>Quick Actions</CardTitle>
                </CardHeader>
                <CardContent className="space-y-2">
                  <Button variant="outline" size="sm" className="w-full justify-start">
                    <Upload className="h-4 w-4 mr-2" />
                    Upload Report
                  </Button>
                  <Button variant="outline" size="sm" className="w-full justify-start">
                    <CalendarIcon className="h-4 w-4 mr-2" />
                    Book Appointment
                  </Button>
                  <Button variant="outline" size="sm" className="w-full justify-start">
                    <Phone className="h-4 w-4 mr-2" />
                    Call Emergency
                  </Button>
                  <Button variant="outline" size="sm" className="w-full justify-start">
                    <FileText className="h-4 w-4 mr-2" />
                    View All Records
                  </Button>
                </CardContent>
              </Card>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}